    box-sizing: border-box;
    color: #222020;
    border-left: 0.25rem solid #858585;
    background: rgb(196, 196, 196);
    width: 14rem;
    height: 3rem;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.12);
}

.round .winners>div.matchups .matchup .participants .participant.winner {
    color: #e6990b;
    border-color: #e6990b;
}

.round .winners>div.matchups .matchup .participants .participant:not(:last-child) {
    border-bottom: thin solid #6d7272;
}

.round .winners>div.matchups .matchup .participants .participant span {
    margin: 0 1.0rem;
    line-height: 3;
    font-size: .6rem;
    font-family: "Roboto Slab";
}

.round .winners>div.connector.filled .line,
.round .winners>div.connector.filled.bottom .merger:after,
.round .winners>div.connector.filled.top .merger:before {
    border-color: #131413;
}

.round .winners>div.connector .line,
.round .winners>div.connector .merger {
    box-sizing: border-box;
    width: 1.5rem;
    display: inline-block;
    vertical-align: top;
}

.round .winners>div.connector .line {
    border-bottom: thin solid #c0c0c8;
    height: 4rem;
}

.round .winners>div.connector .merger {
    position: relative;
    height: 8rem;
}

.round .winners>div.connector .merger:before,
.round .winners>div.connector .merger:after {
    content: "";
    display: block;
    box-sizing: border-box;
    width: 100%;
    height: 50%;
    border: 0 solid;
    border-color: #c0c0c8;
}

.round .winners>div.connector .merger:before {
    border-right-width: thin;
    border-top-width: thin;
}

.round .winners>div.connector .merger:after {
    border-right-width: thin;
    border-bottom-width: thin;
}

.round.quarterfinals .winners:not(:last-child) {
    margin-bottom: 2rem;
}

.round.quarterfinals .winners .matchups .matchup:not(:last-child) {
    margin-bottom: 2rem;
}

.round.semifinals .winners .matchups .matchup:not(:last-child) {
    margin-bottom: 10rem;
}

.round.semifinals .winners .connector .merger {
    height: 16rem;
}

.round.semifinals .winners .connector .line {
    height: 8rem;
}

.round.finals .winners .connector .merger {
    height: 3rem;
}

.round.finals .winners .connector .line {
    height: 1.5rem;
}

